import * as React from "react";
import { makeStyles, Theme, createStyles } from "@material-ui/core/styles";
import Button from "@material-ui/core/Button";

import DialogContent from "@material-ui/core/DialogContent";
import DialogActions from "@material-ui/core/DialogActions";
import Dialog from "@material-ui/core/Dialog";
import axios from "axios";

import TextField from "@material-ui/core/TextField";
import Grid from "@material-ui/core/Grid";
export interface ConfirmationDialogRawProps {
  classes: Record<"paper", string>;
  id: string;
  keepMounted: boolean;
  value: string;
  open: boolean;
  onClose: (value?: string) => void;
}

function ConfirmationDialogRaw(props: ConfirmationDialogRawProps) {
  const { onClose, value: valueProp, open, ...other } = props;
  const [value, setValue] = React.useState(valueProp);
  const radioGroupRef = React.useRef<HTMLElement>(null);

  React.useEffect(() => {
    if (!open) {
      setValue(valueProp);
    }
  }, [valueProp, open]);

  const handleEntering = () => {
    if (radioGroupRef.current != null) {
      radioGroupRef.current.focus();
    }
  };

  const handleCancel = () => {
    onClose();
  };

  const handleOk = () => {
    axios
      .post("/api-react/iniciar-operacion-general/617a4c086b53f915e2103217", {
        flujoBomba: value,
      })
      .then((res) => {});
    onClose(value);
  };

  const handleChange = (event: React.ChangeEvent<any>) => {
    setValue((event.target as any).value);
    console.log(value);
  };

  return (
    <Dialog
      maxWidth="xs"
      onEntering={handleEntering}
      aria-labelledby="confirmation-dialog-title"
      open={open}
      {...other}
    >
      <DialogContent dividers>
        <Grid container justifyContent="center">
          <Grid item xs={12} style={{ margin: "5px" }}>
            Ingresa el flujo de bomba para iniciar el proceso
          </Grid>
          <Grid item xs={10} justifyContent="center">
            <TextField
              required
              id="filled-required"
              label="Flujo Bomba [ml/h]"
              style={{ width: "300px" }}
              type="number"
              onChange={(event) => {
                handleChange(event);
              }}
              disabled={false}
            />
          </Grid>
          <Grid item xs={12}>
            <button onClick={handleOk} className="arrancar">
              Iniciar Proceso
            </button>
          </Grid>
        </Grid>
      </DialogContent>
    </Dialog>
  );
}

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      width: "100%",
      maxWidth: 360,
      backgroundColor: theme.palette.background.paper,
    },
    paper: {
      width: "80%",
      maxHeight: 435,
    },
  })
);
interface propsInterface {
  setFaseModulo(id: number): void;
  setFlujoBomba(id: number): void;
}

export default function ModalIniciar(props: propsInterface) {
  const classes = useStyles();
  const [open, setOpen] = React.useState(true);
  const [value, setValue] = React.useState("Dione");
  const { setFaseModulo, setFlujoBomba } = props;

  const handleClose = (newValue?: string) => {
    setOpen(false);

    if (newValue) {
      setValue(newValue);
      console.log("p2");
      console.log(value);
      console.log(newValue);
      setFlujoBomba(parseFloat(newValue));
    }
    setFaseModulo(1);
  };

  return (
    <div className={classes.root}>
      <ConfirmationDialogRaw
        classes={{
          paper: classes.paper,
        }}
        id="ringtone-menu"
        keepMounted
        open={open}
        onClose={handleClose}
        value={value}
      />
    </div>
  );
}
