import * as React from "react";
import Especifico from "../Especifico/Especifico";
import Login from "../Login/Login";
import General from "./General";
import ModalIniciar from "./ModalIniciar";
import Alerta from "../../components/Alerta/Alerta";

export const GestionFases = () => {
  const [fase, setFase] = React.useState(0);
  const [flujo, setFlujo] = React.useState(0);
  const [estado, setEstado] = React.useState<boolean>(false);
  const [nombre, setNombre] = React.useState("modulo");

  const [text, setText] = React.useState("");

  const handleEstado = (nuevoEstado: boolean) => {
    setEstado(nuevoEstado);
  };
  const handleModuloNombre = (nuevoNombre: string) => {
    setNombre(nuevoNombre);
  };
  const handleText = (nuevoTexto: string) => {
    setText(nuevoTexto);
  };

  const setFaseModulo = (id: number) => {
    setFase(id);
  };
  const setFlujoBomba = (id: number) => {
    setFlujo(id);
    console.log("p1");
    console.log(flujo);
  };

  const renderFunction = (fase: number) => {
    switch (fase) {
      case 0:
        return (
          <ModalIniciar
            setFaseModulo={setFaseModulo}
            setFlujoBomba={setFlujoBomba}
          ></ModalIniciar>
        );
      case 1:
        return (
          <General
            setFaseModulo={setFaseModulo}
            setFlujoBomba={setFlujoBomba}
            flujo={flujo}
            handleEstado={handleEstado}
            handleText={handleText}
            handleModuloNombre={handleModuloNombre}
          ></General>
        );
      case 2:
        return (
          <Especifico
            setFaseModulo={setFaseModulo}
            nombreModulo={nombre}
            handleModuloNombre={handleModuloNombre}
          ></Especifico>
        );
      default:
        return <Login />;
    }
  };
  return (
    <React.Fragment>
      <Alerta texto={text} handleEstado={handleEstado} estado={estado} />
      <div style={{ width: "100%" }}>{renderFunction(fase)}</div>
    </React.Fragment>
  );
};
