import * as React from "react";
import MuiDialogContent from "@material-ui/core/DialogContent";
import DialogContentText from "@material-ui/core/DialogContentText";
import { Theme, withStyles } from "@material-ui/core/styles";
import ErrorIcon from "@material-ui/icons/Error";
import Dialog from "@material-ui/core/Dialog";

interface propsInterface {
  texto?: string;
  estado?: boolean;
  handleEstado(estado: boolean): void;
}
const DialogContent = withStyles((theme: Theme) => ({
  root: {
    padding: theme.spacing(2),
  },
}))(MuiDialogContent);

export default function Alerta(props: propsInterface) {
  const { texto, handleEstado } = props;
  const estado = props.estado;

  return (
    <Dialog
      open={estado ? true : false}
      onClose={() => {
        handleEstado(false);
      }}
      aria-labelledby="alert-dialog-title"
      aria-describedby="alert-dialog-description"
      style={{ borderRadius: "30px" }}
    >
      <DialogContent className="wrong">
        <DialogContentText>
          <div
            style={{
              color: "#a73636",
              fontWeight: "bold",
            }}
          >
            <ErrorIcon style={{ color: "#87150E", marginRight: "4px" }} />
            {texto}
          </div>
        </DialogContentText>
      </DialogContent>
    </Dialog>
  );
}
